import React from 'react'

export default function Quotes() {
    return (
        <div className="cont" id="quoteP">
            <h1>Favorite Quote</h1>
            <div className="box">
                It is our attitude at the beginning of a difficult task which, more than anything else, will affect its successful outcome.
            </div>
            <style jsx>{`
                @keyframes bgGradient {
                    0% {
                        background-position: 0% 50%;
                    }
                    50% {
                        background-position: 100% 50%;
                    }
                    100% {
                        background-position: 0% 50%;
                    }
                }
                .cont {
                    width: 100%;
                    height: auto;
                    position: relative;
                    display: grid;
                   text-align: center;
                   border-top: 30px solid #010101;
                   color: #ffffff;
                   padding-bottom: 90px;
                   background: linear-gradient(-45deg, #ffff00, #E20338, #2F3538, #0351C1);
                   background-size: 400% 400%;
                   animation: bgGradient 15s infinite;
                }
                h1{
                    margin: 50px;
                }
                .box{
                    color: #010101;
                    text-align: left;
                    font-size: 2em;
                    font-family: 'Open Sans',sans-serif;
                    width: 900px;
                    padding: 20px;
                    background: #f3f3f3ba;
                    margin: auto;
                    padding: 1em;
                    border-left: 2px solid #ffff00;
                }
            `}</style>
        </div>
    )
}
