import React from 'react'

export default function LangPopUp(props) {
    return (
        <div className="popupContainer">
            <div className="box">
                <h2>{props.data['select-language']}</h2>
                <div className="selector">
                   <button onClick={() => props.setLang('en')}>English</button> 
                   <button onClick={() => props.setLang('fr')}>Français</button> 
                   <button onClick={() => props.setLang('ar')}>العربية</button> 
                </div>
            </div>
            <div className="close" onClick={() => props.setLang('en')}><span>X</span></div>
            <div className="overlay"></div>
            <style jsx>{`
            .popupContainer{
                position: fixed;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                z-index: 50;
            }
            .overlay{
                position: absolute;
                z-index: 51;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: #111111b3;
                backdrop-filter: blur(5px);
            }
            .close{
                position: absolute;
                z-index: 52;
                top: 27px;
                right: 30px;
                color: #ffffff;
                height: 70px;
                width: 70px;
                display: grid;
            }
            .close:hover{
                color: #ffff00;
            }
            .close span{
                font-weight: bold;
                font-size: 66px;
                margin: auto;
                user-select: none;
                cursor: pointer;
            }
            .box{
                position: absolute;
                z-index: 52;
                width: 600px;
                max-width: 100vw;
                height: 400px;
                background: #000;
                border: 2px solid #ffff00b3;
                border-radius: 2px;
                left: 0; 
                right: 0; 
                top: 0;
                bottom: 0;
                margin: auto;
                padding: 0 20px;
                color:  #ffff00;
            }
            .box .selector{
                display: flex;
                flex-direction: column;
            }
            .box button{
                background: none;
                border: 1px solid #ffffffb3;
                font-size: 20px;
                color: #ffffffb3;
                margin: 20px;
                padding: 15px 25px;
                border-radius: 100px;
                cursor: pointer;
                outline: none;
            }
            .box button:hover, .box button:active, .box button:focus{
                border: 1px solid #ffffff;
                color: #ffffff;
            }
            `}</style>
        </div>
    )
}
