
const fallbackLang = "en";

export const storeLang = (lang = fallbackLang) => {
    if (typeof window !== 'undefined') {
        if(lang){
        window.localStorage.setItem('lang', lang);
        }else{
            window.localStorage.removeItem('lang')
        }
    }
}

export const getLang = () => {
    if (typeof window !== "undefined") {
        return window.localStorage.lang;
    }else{
        return false;
    }
}

export const getData = (lang = fallbackLang) => {
    try {
        return require(`../data/${lang}.json`);
    } catch (err) {
        return require(`../data/${fallbackLang}.json`);
    }
};